import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SecurityAccessGuard } from '../../common/guards/security-access.guard';
import { Ben001Component } from './ben001/ben001.component';

const routes: Routes = [
  {
    path: 'ben001',
    data: { stepId: 'BEN001' },
    // canActivate: [ SecurityAccessGuard ],
    // canDeactivate: [ SecurityAccessGuard ],
    component: Ben001Component
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BeneficiosRoutingModule { }
