import { Component, OnInit, OnDestroy } from '@angular/core';
import { ViewEncapsulation } from '../../../common/components/encaptulation';
import { BaseComponent } from 'src/app/modules/common/components/base/base.component';
import { Payload, BackButton } from 'src/app/modules/common/components/base/base';
import { IBeneficio } from './ben001.model';
import { NPRODUCTO, PAIS, MODULO, LENGUAJE } from 'src/app/modules/common/CONST';


@Component({
  selector: 'app-ben001',
  templateUrl: './ben001.component.html',
  styleUrls: ['./ben001.component.scss', '../../../../../assets/cam/scss/components/beneficios/_ben001.scss'],
  encapsulation: ViewEncapsulation.ShadowDom
})
export class Ben001Component extends BaseComponent implements OnInit, OnDestroy, Payload, BackButton {


  beneficios: IBeneficio[];
  nombreProducto: string;

  ngOnInit(): void {
    this.beneficios = [];
    this.nombreProducto = '';
    this.getBeneficios();
    this.getNombreProducto();
  }

  ngOnDestroy(): void {

  }

  getDataToPayload(): () => { [key: string]: any; } {
    this.disableButton = true;
    return (): { [key: string]: any; } =>
      ({
        codigoPais: this.pais,
        flujo: {
          atras: false,
          continuar: true
        }
      });
  }

  getDataToBackButton(): () => { [key: string]: any; } {
    return (): { [key: string]: any; } => ({
      flujo: {
        atras: true,
        continuar: false
      }
    });
  }

  getBeneficios(): void {
    this.catalogo.data('BEN001', this.params([PAIS, MODULO, LENGUAJE])).subscribe(response => {
      this.beneficios = response ? response : [];
    });
  }

  getNombreProducto(): void {
    this.nombreProducto = this.obser.lastValue(NPRODUCTO);
  }
}
